/**
 * Component: accordion script
 * ------------------------------------------------------------------------------
 * An accordion file that contains scripts to function.
 *
 */
 import classes from '~/config/classes'
 import { debounce } from '~/helpers/general'

 /**
  * DOM selectors.
  */
 const selectors = {
  items: '.accordion__item',
  title: '.accordion__title',
  content: '.accordion__content',
 }

 /**
  * Create a new accordion object.
  */
 export default () => {

  /**
   * Settings.
   */
  const settings = {
    throttle: 250,
    mobileBreakpoint: 768,
  }

   /**
    * DOM node selectors.
    */
   const nodeSelectors = {
    items: document.querySelectorAll(selectors.items),
   }

   /**
    * Initialise component.
    */
   function init() {
     setEventListeners()
     setHeights()
     handleResize()
   }

   /**s
    * Set event listeners.
    */
   function setEventListeners() {
    window.visualViewport.addEventListener(
      'resize',
      debounce(() => handleResize(), 250),
    )

    nodeSelectors.items.forEach((item) => {
      const title = item.querySelector(selectors.title)
      title.addEventListener('click', () => toggleItem(item))
    })
   }

   function setHeights() {
    nodeSelectors.items.forEach((item) => {
      const content = item.querySelector(selectors.content)
      const initHidden = !content.getBoundingClientRect().height

      content.style.height = 'auto'
      const height = content.getBoundingClientRect().height

      if (initHidden) {
        content.style.height = '0px'
      }

      content.setAttribute('js-accordion-height', height)
    })
   }

   /**
    * Toggle accordion item
    * @param {HTMLElement} item
    */
   function toggleItem(item) {
    const content = item.querySelector(selectors.content)
    item.classList.toggle(classes.active)

    if (item.classList.contains(classes.active)) {
      const height = content.getAttribute('js-accordion-height')
      content.style.height = `${height}px`
      return
    }

    content.style.height = '0px'
   }

   /**
    * Show/Hide accordion content on resize
    */
   function handleResize() {
    nodeSelectors.items.forEach((item) => {
      const content = item.querySelector(selectors.content)

      if (
        window.visualViewport.width >= settings.mobileBreakpoint ||
        item.classList.contains(classes.active)
      ) {
        content.style.height = 'auto'
        return
      }

      content.style.height = '0px'
    })
   }

   /**
    * Expose public interface.
    */
   return Object.freeze({
     init,
   })
 }
