/**
 * Component: Checkout Terms
 * ------------------------------------------------------------------------------
 * Script to prevent users from checking out until agreeing to terms.
 *
 */
import classes from '~/config/classes'

/**
 * DOM selectors.
 */
const selectors = {
  continueButton: '.shown-if-js .step__footer__continue-btn',
  footer: '.step__footer',
  section: '.section--terms',
  yes: '#terms_agree_yes',
}

/**
 * Create a new example object.
 */
export default () => {

  const dictionary = [
    'header',
    'content',
    'yes',
    'no',
    'error',
  ]

  /**
   * Track state of terms
   */
  const state = {
    selected: false,
  }

  /**
   * DOM node selectors.
   */
  const nodeSelectors = {
    continueButton: document.querySelector(selectors.continueButton),
    footer: document.querySelector(selectors.footer),
  }

  /**
   * Initialize component.
   */
  function init() {
    if (Shopify.Checkout.step === 'payment_method') {
      createTerms()
      setEventListeners()
    }
  }

  /**
   * Set Event Listeners
   */
  function setEventListeners() {
    document.addEventListener('click', (event) => {
      if (
        event.target &&
        event.target.getAttribute('type') === 'submit'
      ) {
        handleSubmit(event)
      }
    })

    document.addEventListener('change', (event) => {
      if (event.target && event.target.classList.contains('input-radio--choice')) {
        updateSelection()
      }
    })
  }

  /**
   * Prevent checkout is Terms are not accepted
   */
  function handleSubmit(event) {
    if (state.selected) {
      return
    }

    event.preventDefault()
    setError()
  }

  /**
   * Hide/Render error based on state.
   */
  function setError() {
    const section = document.querySelector(selectors.section)

    if (state.selected) {
      section.classList.remove(classes.error)
      nodeSelectors.continueButton.classList.remove('btn--disabled')
      nodeSelectors.continueButton.disabled = false
      return
    }

    section.classList.add(classes.error)
    nodeSelectors.continueButton.classList.add('btn--disabled')
    nodeSelectors.continueButton.disabled = true
  }

  /**
   * Update state when term selection is updated
   */
  function updateSelection() {
    const checkbox = document.querySelector(selectors.yes)
    state.selected = checkbox.checked

    setError()
  }

  /**
   * Inject Terms UI into DOM.
   */
  function createTerms() {
    const template = getTemplate()
    nodeSelectors.footer.insertAdjacentHTML('beforebegin', template)
  }

  /**
   * Return Terms UI template.
   */
  function getTemplate() {
    let template =  `
      <div class="section section--terms">
        <div class="section__header">
          <h2 class="section__title">[[header]]</h2>
          <p class="section__text">[[content]]</p>
        </div>
        <div class="section__content">
          <fieldset class="content-box content-box--terms">
            <div class="radio-wrapper content-box__row">
              <div class="radio__input">
                <input class="input-radio input-radio--choice" type="radio" name="terms_agree" id="terms_agree_yes">
              </div>
              <label class="radio__label content-box__emphasis" for="terms_agree_yes">
                [[yes]]
              </label>
            </div>
            <div class="radio-wrapper content-box__row">
              <div class="radio__input">
                <input class="input-radio input-radio--choice" type="radio" name="terms_agree" id="terms_agree_no">
              </div>
              <label class="radio__label content-box__emphasis" for="terms_agree_no">
                [[no]]
              </label>
            </div>
          </fieldset>
          <p class="section__text section__text--error">[[error]]</p>
        </div>
      </div>
    `

    dictionary.forEach((entry) => {
      const translation = window.cnvs.strings.checkout.terms[entry]
      const text = new DOMParser().parseFromString(translation, 'text/html')
      template = template.replace(`[[${entry}]]`, text.documentElement.textContent)
    })

    return template
  }

  /**
   * Expose public interface.
   */
  return Object.freeze({
    init,
  })
}
