/**
 * Component: Checkout business fields
 * ------------------------------------------------------------------------------
 *
 */

import { createApp } from 'vue'
import CheckoutBusinessFields from '~async/checkout-business-fields/checkout-business-fields.vue'

/**
 * DOM selectors.
 */
const selectors = {
  fieldsContainer: 'checkoutBusinessFields',
  address1: '[data-address-field="address1"]',
}

/**
 * Create a new example object.
 */
export default () => {
  const { businessFields } = window.cnvs
  const { step } = Shopify.Checkout
  let app = {}

  /**
   * DOM node selectors.
   */
  const nodeSelectors = {
    address1: document.querySelector(selectors.address1),
  }

  /**
   * Append business fields to checkout step 1.
   */
  function appendBusinessFieldsContainer() {
    nodeSelectors.address1.insertAdjacentHTML('beforebegin', `
      <div id="${selectors.fieldsContainer}" class="section__business-fields">
        <checkout-business-fields
          :customer-max-chars="${businessFields.customerMaxChars}"
          :abn-max-chars="${businessFields.abnMaxChars}"
          abn-field-name="${businessFields.abnFieldName}"
        />
      </div>
    `)
  }

  /**
   * Initialize Vue app.
   */
  function initVueFields() {
    app = createApp({
      components: {
        CheckoutBusinessFields,
      },

    })

    app.mount(`#${selectors.fieldsContainer}`)
  }

  /**
   * Initialise component.
   */
  function init() {
    if (
      step !== 'contact_information' ||
      (step === 'contact_information' && !businessFields.isEnabled)
    ) {
      return
    }

    appendBusinessFieldsContainer()
    initVueFields()
  }

  /**
   * Expose public interface.
   */
  return Object.freeze({
    init,
  })
}
