/**
 * Component: Checkout postal code validator
 * ------------------------------------------------------------------------------
 *
 */

import { createApp } from 'vue'
import CheckoutPostalCodeValidator from '~async/checkout-postal-code-validator/checkout-postal-code-validator.vue'

/**
 * DOM selectors.
 */
const selectors = {
  postalCodeContainer: 'checkoutPostalCodeValidator',
  zip: 'checkout_shipping_address_zip',
}

/**
 * Create a new example object.
 */
export default () => {
  const { step } = Shopify.Checkout
  const {
    showPostalCodeValidatorOnCheckout: showValidator,
  } = cnvs.settings
  let app = {}

  /**
   * DOM node selectors.
   */
  const nodeSelectors = {
    zip: document.getElementById(selectors.zip),
  }

  /**
   * Append postal code validator to checkout step 1.
   */
  function appendPostalCodeValidator() {
    nodeSelectors.zip.insertAdjacentHTML('afterend', `
      <div id="${selectors.postalCodeContainer}">
        <checkout-postal-code-validator />
      </div>
    `)
  }

  /**
   * Initialize Vue app.
   */
  function initVueFields() {
    app = createApp({
      components: {
        CheckoutPostalCodeValidator,
      },

    })

    app.mount(`#${selectors.postalCodeContainer}`)
  }

  /**
   * Initialise component.
   */
  function init() {
    if (step !== 'contact_information' || !showValidator) {
      return
    }

    appendPostalCodeValidator()
    initVueFields()
  }

  /**
   * Expose public interface.
   */
  return Object.freeze({
    init,
  })
}
