/**
 * Component: Checkout Shipping Tooltips script
 * ------------------------------------------------------------------------------
 * Enable tooltips for shipping methods on checkout.
 */
import classes from '~/config/classes'

/**
 * DOM selectors.
 */
const selectors = {
  methodTitles: '[data-shipping-method-label-title]',
  methodDesc: '[data-shipping-method-label-title] .small-text',
  summary: '.total-line.total-line--shipping .total-line__name',
  toolTips: '.shipping-tooltip',
}

export default () => {

  /**
   * DOM node selectors.
   */
  const nodeSelectors = {
    titles: document.querySelectorAll(selectors.methodTitles),
    summary: document.querySelector(selectors.summary),
    toolTips: document.querySelectorAll(selectors.toolTips),
  }

  /**
   * Initialize component.
   */
  function init() {
    if (Shopify.Checkout.step === 'shipping_method') {
      createMethodTooltips()
    }

    if (
      Shopify.Checkout.step === 'shipping_method' ||
      Shopify.Checkout.step === 'payment_method'
    ) {
      createSummaryTooltip()
    }

    setEventListeners()
  }

  /**
   * Set Event Listeners.
   */
  function setEventListeners() {
    document.addEventListener('click', (e) => {
      const toolTips = document.querySelectorAll(selectors.toolTips)
      if (isTrigger(e.target)) {
        toggleToolTip(toolTips, e.target)
      }

      const hasActiveElement = Array.from(toolTips).find((el) => {
        return el.classList.contains(classes.active)
      })
      const isMobileOverlay = !e.target.closest(".shipping-tooltip") && hasActiveElement && !isTrigger(e.target)

      if ( isClose(e.target) || isMobileOverlay) {
        toolTips.forEach((toolTip) => {
          toolTip.classList.remove(classes.active)
        })
      }
    })

    document.addEventListener('mouseover', (e) => {
      if (isTrigger(e.target)) {
        e.target.nextElementSibling.classList.add(classes.hovered)
      }
    })

    document.addEventListener('mouseout', (e) => {
      if (isTrigger(e.target)) {
        e.target.nextElementSibling.classList.remove(classes.hovered)
      }
    })
  }

  /**
   * Determine if element is Open Trigger
   * @param {HTMLElement} target
   * @returns {Boolean}
   */
  function isTrigger(target) {
    return target && target.classList.contains('shipping-tooltip-trigger')
  }

  /**
   * Determine if element is Close Trigger
   * @param {HTMLElement} target
   * @returns {Boolean}
   */
  function isClose(target) {
    return target && target.classList.contains('shipping-tooltip-close')
  }

  /**
   * Toggles tooltip class for mobile.
   * @param {Array} toolTips - Array of tooltip elements.
   * @param {HTMLElement} trigger - selected tooltip trigger.
   */
  function toggleToolTip(toolTips, trigger) {
    toolTips.forEach((el) => {
      if (el !== trigger.nextElementSibling) {
        el.classList.remove(classes.active)
      }
    })

    trigger.nextElementSibling.classList.toggle(classes.active)
  }

  /**
   * Create tooltips for Shipping Methods.
   */
  function createMethodTooltips() {
    nodeSelectors.titles.forEach((method) => {
      const title = method.getAttribute('data-shipping-method-label-title')
      const meta = window.shipping_meta.methods.find((data) => data.title === title)
      let content = ''

      if (meta) {
        content += `<p>${meta.meta}</p>`
      }

      if (window.shipping_meta.showInMethods) {
        content += window.shipping_meta.disclaimer
      }

      if (content.length > 0) {
        method.firstElementChild
          ? method.firstElementChild.insertAdjacentHTML('beforebegin', `${icon(content)}`)
          : method.insertAdjacentHTML('beforeend', `${icon(content)}`)
        }
    })
  }

  /**
   * Create tooltip for Summary.
   */
  function createSummaryTooltip() {
    if (!window.shipping_meta.disclaimer) {
      return
    }

    nodeSelectors.summary.innerHTML += `${icon(window.shipping_meta.disclaimer)}`
  }

  /**
   * Return template for hover icon
   */
  function icon(content) {
    return `
      <span class="shipping-tooltip-wrapper">
        <a class="shipping-tooltip-trigger" href="javascript:void(0);">
          <svg class="icon icon__info" width="13" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16">
            <path d="M8 0C3.6 0 0 3.6 0 8s3.6 8 8 8 8-3.6 8-8-3.6-8-8-8zm.7 13H6.8v-2h1.9v2zm2.6-7.1c0 1.8-1.3 2.6-2.8 2.8l-.1 1.1H7.3L7 7.5l.1-.1c1.8-.1 2.6-.6 2.6-1.6 0-.8-.6-1.3-1.6-1.3-.9 0-1.6.4-2.3 1.1L4.7 4.5c.8-.9 1.9-1.6 3.4-1.6 1.9.1 3.2 1.2 3.2 3z"></path>
          </svg>
        </a>
        ${tooltip(content)}
      </span>
    `
  }

  /**
   * Return template for tooltip
   */
  function tooltip(content) {
    return `
      <div class="shipping-tooltip">
        ${content}
        <a class="shipping-tooltip-close" href="javascript:void(0);">
          <svg width="9" class="icon icon__close" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 9.33 9.33">
            <path d="M9.33,.94l-.94-.94-3.73,3.73L.94,0,0,.94l3.73,3.73L0,8.39l.94,.94,3.73-3.73,3.73,3.73,.94-.94-3.73-3.73L9.33,.94Z"/>
          </svg>
        </a>
      </div>
    `
  }

  /**
  * Expose public interface.
  */
  return Object.freeze({
    init,
  })
}