<template>
  <div
    v-if="isContactInfoStep && postalCodeField"
    class="postal-code-validator"
  >
    <button
      v-if="isFormSubmitted && ((!isZipValid && postalCodeValue !== '') || result === false)"
      type="button"
      class="postal-code-validator__clear"
      @click="clearForm"
    >
      <IconCloseFill />
    </button>
    <p
      v-if="!isChangedByUser && postalCodeShopifyError"
      class="postal-code-validator__error-message"
      v-text="postalCodeShopifyError?.innerText"
    />
    <template v-else>
      <p
        v-if="isFormSubmitted && !isZipValid && postalCodeValue !== ''"
        class="postal-code-validator__error-message"
        v-text="translations.postal_code_validator.error_message"
      />
      <p
        v-if="isLoading"
        class="postal-code-validator__loading-message"
        v-text="translations.postal_code_validator.loading_message"
      />
      <p
        v-if="isFormSubmitted && result === false"
        class="postal-code-validator__error-message"
        v-text="translations.postal_code_validator.invalid_zip_message"
      />
      <div
        v-if="isFormSubmitted && result === true"
        class="postal-code-validator__success-notice"
      >
        <div
          class="postal-code-validator__success-message"
          v-text="translations.postal_code_validator.valid_zip_message"
        />
      </div>
    </template>
  </div>
  <span v-else />
</template>
<script>

 /**
 * Vue: Checkout postal code validator field (checkout-postal-code-validator)
 * -----------------------------------------------------------------------------
 *
 */
import IconCloseFill from '~icons/misc/close--fill.svg'

export default {
  name: 'CheckoutPostalCodeValidator',
  components: {
    IconCloseFill,
  },
  data() {
    const { checkout } = window.cnvs.strings
    return {
      postalCodeField: null,
      postalCodeFieldId: 'checkout_shipping_address_zip',
      postalCodeValue: '',
      postalCodeWrapper: null,
      postalCodeErrorSelector: "[id=error-for-zip]",
      postalCodeShopifyError: null,
      postalCodeWrapperSelector: "[data-address-field='zip']",
      submitButton: null,
      submitButtonId: 'continue_button',
      translations: {
        ...checkout,
      },
      isChangedByUser: false, // True after input changes
      isFormSubmitted: false, // True after submission, until a change is made
      isLoading: false, // True after submission if zip is valid, until a change is made
      result: null, // Postal code validity result
    }
  },
  computed: {
    isContactInfoStep() {
      return Shopify.Checkout.step === 'contact_information'
    },

    isZipValid() {
      const zipRegex = /^(?:(?:[2-8]\d|9[0-7]|0?[28]|0?9(?=09))(?:\d{2}))$/
      return zipRegex.test(this.postalCodeValue)
    },

    /**
     * The postal code app's domain name, used for API calls
     * @returns {Boolean}
     */
    appDomain() {
      return cnvs.credentials.hyperx_stacklet.domain
    },
  },
  watch: {
    result(newResult) {
      if (newResult === false) {
        this.addPostalCodeWrapperError()
      } else {
        this.removePostalCodeWrapperError()
      }
    },
    postalCodeWrapper() {
      if (this.result === false) {
        this.addPostalCodeWrapperError()
      } else {
        this.removePostalCodeWrapperError()
      }
    },
  },
  mounted() {
    if (this.isContactInfoStep) {
      this.setSubmitButton()
      this.setPostalCodeWrapper()
      this.setPostalCodeField()
      this.setPostalCodeShopifyError()
      this.handleSubmit()
    }
  },
  methods: {
    setPostalCodeField() {
      this.postalCodeField = document.getElementById(this.postalCodeFieldId)
      this.postalCodeField.setAttribute('maxlength', 4)
      this.postalCodeField.addEventListener('keyup', this.handleInputKeyup)
      this.postalCodeField.addEventListener('change', this.handleInputChange)
      this.setPostalCodeValue()
    },
    setPostalCodeShopifyError() {
      this.postalCodeShopifyError = document.querySelector(this.postalCodeErrorSelector)
    },

    setPostalCodeWrapper() {
      this.postalCodeWrapper = document.querySelector(this.postalCodeWrapperSelector)
    },

    addPostalCodeWrapperError() {
      this.postalCodeWrapper.classList.add('field--error')
    },

    removePostalCodeWrapperError() {
      this.postalCodeWrapper.classList.remove('field--error')
    },

    setPostalCodeValue() {
      this.postalCodeValue = this.postalCodeField.value
    },

    setSubmitButton() {
      this.submitButton = document.getElementById(this.submitButtonId)
      this.submitButton.disabled = true
      this.submitButton.classList.add('btn--disabled')
    },

    handleInputChange() {
      this.setPostalCodeValue()
      this.isFormSubmitted = false
      this.isChangedByUser = true
      this.handleSubmit()
    },

    handleInputKeyup() {
      this.setPostalCodeValue()
      this.isFormSubmitted = false
      this.isLoading = false
      this.result = null
      this.submitButton.disabled = true
      this.submitButton.classList.add('btn--disabled')
    },

    async handleSubmit() {
      if (!this.isFormSubmitted) {
        this.isFormSubmitted = true
        this.result = null
        this.submitButton.disabled = true
      this.submitButton.classList.add('btn--disabled')

        if (this.isZipValid) {
          this.isLoading = true
          // Get postal code validity
          this.result = await fetch(`https://${
            this.appDomain
          }/api/postcode-validator/postcode-lookup?postcode=${this.postalCodeValue}`)
            .then((result) => result.json())
            .catch(() => false) // In case of total failure
          this.isLoading = false
          if (this.result === true) {
            this.submitButton.disabled = false
            this.submitButton.classList.remove('btn--disabled')
          }
        }
      }
    },

    clearForm() {
      this.isFormSubmitted = false
      this.isLoading = false
      this.result = null
      this.postalCodeField.value = ''
      this.setPostalCodeValue()
    },
  },
}
</script>
