/**
 * Component: Checkout name fields
 * ------------------------------------------------------------------------------
 *
 */

import { createApp } from 'vue'
import CheckoutNameFields from '~async/checkout-name-fields/checkout-name-fields.vue'

/**
 * DOM selectors.
 */
const selectors = {
  noteContainer: 'checkoutNameFieldsNote',
  lastName: "[data-address-field='last_name']",
}

/**
 * Create a new example object.
 */
export default () => {
  const { nameFields } = window.cnvs
  const { step } = Shopify.Checkout
  let app = {}

  /**
   * DOM node selectors.
   */
  const nodeSelectors = {
    lastName: document.querySelector(selectors.lastName),
  }

  /**
   * Append name fields note to checkout step 1.
   */
  function appendNameFieldsNote() {
    nodeSelectors.lastName.insertAdjacentHTML('afterend', `
      <div id="${selectors.noteContainer}">
        <checkout-name-fields :max-chars="${nameFields.maxChars}" />
      </div>
    `)
  }

  /**
   * Initialize Vue app.
   */
  function initVueFields() {
    app = createApp({
      components: {
        CheckoutNameFields,
      },

    })

    app.mount(`#${selectors.noteContainer}`)
  }

  /**
   * Initialise component.
   */
  function init() {
    if (
      step !== 'contact_information' ||
      (step === 'contact_information' && !nameFields.isEnabled)
    ) {
      return
    }

    appendNameFieldsNote()
    initVueFields()
  }

  /**
   * Expose public interface.
   */
  return Object.freeze({
    init,
  })
}
