/**
 * Component: Checkout address field
 * ------------------------------------------------------------------------------
 *
 */

import { createApp } from 'vue'
import CheckoutAddressField from '~async/checkout-address-field/checkout-address-field.vue'

/**
 * DOM selectors.
 */
const selectors = {
  noteContainer: 'checkoutAddressFieldNote',
  address1: 'checkout_shipping_address_address1',
}

/**
 * Create a new example object.
 */
export default () => {
  const { addressField } = window.cnvs
  const { step } = Shopify.Checkout
  let app = {}

  /**
   * DOM node selectors.
   */
  const nodeSelectors = {
    address1: document.getElementById(selectors.address1),
  }

  /**
   * Append address field to checkout step 1.
   */
  function appendAddressFieldContainer() {
    nodeSelectors.address1.insertAdjacentHTML('afterend', `
      <div id="${selectors.noteContainer}">
        <checkout-address-field :max-chars="${addressField.maxChars}" />
      </div>
    `)
  }

  /**
   * Initialize Vue app.
   */
  function initVueFields() {
    app = createApp({
      components: {
        CheckoutAddressField,
      },

    })

    app.mount(`#${selectors.noteContainer}`)
  }

  /**
   * Initialise component.
   */
  function init() {
    if (
      step !== 'contact_information' ||
      (step === 'contact_information' && !addressField.isEnabled)
    ) {
      return
    }

    appendAddressFieldContainer()
    initVueFields()
  }

  /**
   * Expose public interface.
   */
  return Object.freeze({
    init,
  })
}
