/**
 * Layout: Checkout
 * -----------------------------------------------------------------------------
 * Entry file for all global checkout scripts.
 *
 */
import { getState } from '@shopify/theme-cart'
import classes from '~/config/classes'

/**
 * Core scripts.
 */
import Bugsnag from '~/core/bugsnag'

/**
 * Additional scripts.
 */
import { keyboardTabbable } from '~/helpers/checkout'
import { on, debounce } from '~/helpers/general'
import { sendCouponApplied, sendConversion, sendOrderStatus } from '../helpers/datalayer'
import CheckoutShippingTooltips from '../components/checkout-shipping-tooltips'
import CheckoutTerms from '../components/checkout-terms'
import Accordion from '../components/accordion'
import CheckoutPostalCodeValidator from '../components/checkout-postal-code-validator'
import CheckoutBusinessFields from '../components/checkout-business-fields'
import CheckoutAddressField from '../components/checkout-address-field'
import CheckoutNameFields from '../components/checkout-name-fields'


/**
 * Global Canvas functions.
 */
window.cnvs = window.cnvs || {}

/**
 * DOM node selectors.
 */
const nodeSelectors = {
  breadCrumb: document.querySelector('.breadcrumb'),
  breadCrumbItems: document.querySelectorAll('.breadcrumb__item'),
  previousLink: document.querySelector('.step__footer__previous-link-content'),
  needHelp: document.querySelector('.step__footer__info'),
  inputs: document.querySelectorAll('.checkout input'),
  cartItemProperites: document.querySelectorAll('.product__description .order-summary__small-text'),
  reductionCode: document.getElementById('checkout_reduction_code'),
  shippingAddressPhone: document.getElementById('checkout_shipping_address_phone'),
  logInLink: document.querySelector('.layout-flex__item a'),
  payNowBtn: document.querySelector('.step__footer__continue-btn .btn__content'),
  applyBtn: document.querySelectorAll('.field__input-btn'),
  orderSummary: document.querySelector('.order-summary-toggle'),
}

/**
 * Checkout functions.
 */
export function Checkout() {

  /**
   * Initialise component.
   * @param {Boolean} pageLoad - If the functions are being run on page load,
   * use to run functions only once where running them repeatedly would cause
   * issues such as duplicated content etc.
   */
  async function init(pageLoad = false) {
    if (pageLoad) {
      Bugsnag().init()
      keyboardTabbable().init()
      CheckoutShippingTooltips().init()
      CheckoutTerms().init()
      mutationObserver()
      handleGiftProductProperties()
      Accordion().init()
      CheckoutPostalCodeValidator().init()
      CheckoutNameFields().init()
      CheckoutBusinessFields().init()
      CheckoutAddressField().init()
      setEventListeners()
    }

    /**
     * Early returns for pages that don't support getState().
     */
    if (Shopify.Checkout.step === 'thank_you') {
      orderConfirmation(pageLoad)
      return
    }

    if (Shopify.Checkout.isOrderStatusPage) {
      orderStatusPage(pageLoad)
      return
    }

    /**
     * Load cart data.
     */
    try {
      const cartData = await getState()

      switch (Shopify.Checkout.step) {
        case 'contact_information':
          contactInformation(pageLoad, cartData)
          break

        case 'shipping_method':
          shippingMethod(pageLoad, cartData)
          break

        case 'payment_method':
          paymentMethod(pageLoad, cartData)
          break
      }

    } catch (error) {
      reportError('Failed to run checkout scripts', error)
    }
  }

  /**
   * Elements to change for step one, information.
   */
  function handleGiftProductProperties() {
    if (!nodeSelectors.cartItemProperites) {
      return
    }

    [...nodeSelectors.cartItemProperites].forEach((property) => {
      if (property.innerText === '') {
        return
      }

      const orgGiftText = property.innerText

      if (orgGiftText.indexOf('gift_message_to:') === 0) {
        property.innerText = orgGiftText.replace('gift_message_to:', 'To:')
      } else if (orgGiftText.indexOf('gift_message_from:') === 0) {
        property.innerText = orgGiftText.replace('gift_message_from:', 'From:')
      } else if (orgGiftText.indexOf('gift_message_message:') === 0) {
        property.innerText = orgGiftText.replace('gift_message_message:', 'Gift Message:')
      } else if (orgGiftText.indexOf('pre_order:') === 0) {
        if (!cnvs.settings.preOrderCheckout) {
          property.innerText = ''

          return
        }

        property.innerText = orgGiftText.replace('pre_order:', '')
      } else if (orgGiftText.indexOf('ecoTax:') === 0) {
        property.innerText = orgGiftText.replace('ecoTax:', '')
      } else {
        property.innerText = orgGiftText
      }
    });

    [...nodeSelectors.applyBtn].forEach((btn) => {
      btn.setAttribute('aria-label', 'button')
    })

    if (!nodeSelectors.reductionCode) {
      return
    }

    nodeSelectors.reductionCode.placeholder = 'Gift Card or Discount Code'

    if (!nodeSelectors.logInLink) {
      return
    }

    nodeSelectors.logInLink.innerText = 'Login'
  }

  /**
   * Mark breadcrumb as completed by step
   */
   function updateBreadCrumb(step = 0) {
    for (let i = 0; i <= step; i++) {
      if (nodeSelectors.breadCrumbItems.length === 3 && i === 1) {
        // To handle the case of when shipping step will be ignored
        nodeSelectors.breadCrumb.classList.add(`breadcrumb--step${i}-half`)
      } else {
        nodeSelectors.breadCrumb.classList.add(`breadcrumb--step${i}`)
      }

      if (!nodeSelectors.breadCrumbItems[i]) {
        return
      }
      nodeSelectors.breadCrumbItems[i].classList.add('breadcrumb__item--maroon')
    }

    nodeSelectors.breadCrumb.classList.add('is-completed')
    nodeSelectors.breadCrumbItems.forEach((item) => {
      on('click', item, (event) => handleClickBreadcrumbEvents(event))
    })

    if (step === 3) {
      if (!nodeSelectors.payNowBtn) {
        return
      }
      nodeSelectors.payNowBtn.parentElement.removeAttribute('id')
    }
  }

  /**
   * Breadcrumb li element click event handler
   */
  function handleClickBreadcrumbEvents(event) {
    event.preventDefault()

    const parentElement = event.target
    let link = parentElement.querySelector('.breadcrumb__link')
    if (parentElement.tagName === 'A') {
      link = parentElement
    }

    if (!link) {
      return
    }

    window.location.href = link.getAttribute('href')
  }

  /**
   * Add new elements to the Shipping and Payment steps.
   */
  function addLeftArrow() {
    const arrowLeft = document.createElement('div')
    const link = document.querySelector('.step__footer__previous-link')
    arrowLeft.classList.add('arrow-left')
    arrowLeft.innerHTML = `
      <svg class="icon icon__arrow icon__arrow--left" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
        <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z"/>
      </svg>
    `

    if (!link) {
      return
    }

    link.insertBefore(arrowLeft, nodeSelectors.previousLink)
  }

  /**
   * Add new elements to the Shipping and Payment steps.
   */
  function addQuestionMark() {
    const questionMark = document.createElement('div')
    const link = document.querySelector('.step__footer__info')
    questionMark.classList.add('question-mark')
    questionMark.innerHTML = `
      <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
        <circle cx="9" cy="9" r="9" fill="#999999"/>
        <path d="M5.625 6.36986H7.34576C7.40434 5.47653 8.0121 4.90538 8.95669 4.90538C9.87931 4.90538 10.4944 5.45456 10.4944 6.21609C10.4944 6.941 10.1868 7.32909 9.27887 7.87827C8.26838 8.47138 7.84368 9.1304 7.90958 10.2288L7.91691 10.7267H9.6157V10.3093C9.6157 9.58438 9.88663 9.21094 10.8459 8.64712C11.8417 8.05401 12.3982 7.27051 12.3982 6.15018C12.3982 4.53194 11.0582 3.375 9.05188 3.375C6.87713 3.375 5.68358 4.63445 5.625 6.36986ZM8.84685 14.2195C9.59373 14.2195 10.0624 13.7581 10.0624 13.0552C10.0624 12.3449 9.59373 11.8836 8.84685 11.8836C8.11461 11.8836 7.63133 12.3449 7.63133 13.0552C7.63133 13.7581 8.11461 14.2195 8.84685 14.2195Z" fill="white"/>
      </svg>
    `

    if (!link) {
      return
    }

    nodeSelectors.needHelp.insertAdjacentElement('afterbegin', questionMark)
  }

  /**
   * Set event listeners.
   */
  function setEventListeners() {
    on('click', nodeSelectors.orderSummary, () => {
      setTimeout(() => { handleGiftProductProperties() }, 200)
    })

    document.body.addEventListener('submit', (ev) => {
      const discount = ev.target.querySelector('#checkout_reduction_code')

      sanitizePhoneNumber()

      if (!discount) {
        return
      }

      sendCouponApplied(discount.value)
    })

    if (Shopify.Checkout.step !== 'contact_information') {
      return
    }

    const address1 = [...nodeSelectors.inputs].filter((input) => input.id === 'checkout_shipping_address_address1')[0]
    const address2 = [...nodeSelectors.inputs].filter((input) => input.id === 'checkout_shipping_address_address2')[0];

    [
      'propertychange',
      'change',
      'click',
      'keyup',
      'input',
      'paste',
    ].forEach((inputEvent) => {
      on(inputEvent, address1, (event) => handleInputEvents(event))
      on(inputEvent, address2, (event) => handleInputEvents(event))
    })
  }

  /**
   * Handle events for form address inputs.
   * @param {event} Any event relating to the input.
   */
  function handleInputEvents(event) {
    const input = event.target

    if (typeof input.value !== 'string') {
      return
    }

    input.classList.add(classes.active)
  }

  /**
   * Sanitize phone number.
   */
  function sanitizePhoneNumber() {
    if (!nodeSelectors.shippingAddressPhone) {
      return
    }

    const phone = nodeSelectors.shippingAddressPhone.value.replace(/[\D]/g, '')

    nodeSelectors.shippingAddressPhone.value = phone
  }

  /**
   * Mutation observer to detect when DOM is idle, then add elements.
   */
  function mutationObserver() {
    const target = document.querySelector('body')
    const config = {
      attributes: true,
      childList: true,
      subtree: true,
    }

    const observer = new MutationObserver(debounce(() => {
      observer.disconnect()
      handleGiftProductProperties()
      addLeftArrow()
      addQuestionMark()
    }, 150))

    observer.observe(target, config)
  }

  /**
   * Functions to run on the contact information step.
   * @param {Boolean} pageLoad - Function is being run on page load.
   * @param {Object} cartData - Cart response data.
   */
  function contactInformation() {
    updateBreadCrumb(1)
  }

  /**
   * Functions to run on the shipping method step.
   * @param {Boolean} pageLoad - Function is being run on page load.
   * @param {Object} cartData - Cart response data.
   */
  function shippingMethod() {
    updateBreadCrumb(2)
  }

  /**
   * Functions to run on the payment method step.
   * @param {Boolean} pageLoad - Function is being run on page load.
   * @param {Object} cartData - Cart response data.
   */
  function paymentMethod() {
    updateBreadCrumb(3)
  }

  /**
   * Functions to run on the order confirmation page.
   * - Cart data is not available on these pages.
   * @param {Boolean} pageLoad - Function is being run on page load.
   */
  function orderConfirmation() {
    sendConversion(Shopify.checkout)
  }

  /**
   * Functions to run on the order status page.
   * - Cart data is not available on these pages.
   * @param {Boolean} pageLoad - Function is being run on page load.
   */
  function orderStatusPage() {
    sendOrderStatus()
  }

  /**
   * Expose public interface.
   */
  return Object.freeze({
    init,
  })
}

[
  'DOMContentLoaded',
  'page:change',
  'page:update',
].forEach((event) => {
  document.addEventListener(event, () => {
    Checkout().init(event === 'DOMContentLoaded')
  })
})
